import React from "react";
import { View, Text, TouchableOpacity } from "react-native";
import UIImage from "./UIImage";

export default ({ title, children, onBack,style }: any) => {
  return (
    <View
      style={{
        ...(style || {}),
        paddingVertical: 15,
        paddingHorizontal: 25,
        flexBasis: 75,
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "space-between"
      }}
    >
      <View style={{ flexDirection: "row", alignItems: "center" }}>
        {onBack && (
          <TouchableOpacity
            style={{
              width: 20,
              height: 20,
              marginLeft: -10,
              marginRight: 10,
              marginTop: 3
            }}
            onPress={onBack}
          >
            <UIImage
              style={{
                width: 20,
                height: 20
              }}
              source={require("@app/libs/ui/imgs/back.png")}
            />
          </TouchableOpacity>
        )}
        <Text style={{ fontSize: 22, color: "#555" }}>
          {(title || "").trim()}
        </Text>
      </View>
      <View>{children}</View>
    </View>
  );
};
